/* Number Input */
.number-input input[type="number"] {
  -webkit-appearance: textfield;
    -moz-appearance: textfield;
          appearance: textfield;
}
.number-input input[type=number]::-webkit-inner-spin-button,
.number-input input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}
.number-input {
  margin-bottom: 0px;
  float: right;
  text-align: right;
}
.number-input button {
  -webkit-appearance: none;
  background-color: transparent;
  border: none;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 0;
  position: relative;
}
.number-input button:before,
.number-input button:after {
  display: inline-block;
  position: absolute;
  content: '';
  height: 2px;
  transform: translate(-50%, -50%);
}
.number-input button.plus:after {
  transform: translate(-50%, -50%) rotate(90deg);
}
.number-input input[type=number] {
  text-align: center;
}
.number-input.number-input {
  /* border: 1px solid #ced4da; */
  width: 200px;
  border-radius: .25rem;
}
.number-input.number-input button {
  width: 2.6rem;
  height: 32px;
  vertical-align: top;
}
.number-input.number-input button.minus {
  padding-left: 10px;
}
.number-input button.minus, .number-input button.plus {
  background-color: #bb9277;
}
.number-input button.minus.disabled, .number-input button.plus.disabled {
  background-color: #d8c8bd;
}
.number-input.number-input button:before,
.number-input.number-input button:after {
  width: .7rem;
  background-color: #fff;
}
.number-input.number-input input[type=number] {
  width: 55px;
  padding: .5rem;
  /* border: 1px solid #ced4da; */
  border-top: 1px solid #d8c8bd;
  border-bottom: 1px solid #d8c8bd;
  border-left: 1px solid #d8c8bd;
  border-right: 1px solid #d8c8bd;
  border-width: 0px;
  font-size: 1rem;
  font-weight: 600;
  height: 2rem;
  color: #495057;
}

.input-readonly {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;  
}

