.wrap-main > .card.wrap-summary > .card-body {
    border-width: 1px;
}
.wrap-summary .summary-list-group {
    padding-left: 20px;
    list-style: none;
}
.wrap-summary .list-group-item {
    margin-top: 20px;
    border-top: 2px solid #c8942b;
}
.list-group-item h3 {
    padding-left: 13px;
    color: #f48222;
    font-size: 1.2rem;
}
.summary-list.header {
    color: #f48222;
    font-weight: 600;
    border-top: 1px solid #c6c6c6;
    border-bottom: 1px solid #ececec;
}
.summary-list {
    font-size: 15px;
    border-bottom: 1px solid #ececec;
}
.summary-list:hover {
    background-color: #ececec;
}
.summary-list .item {
    /* width: 500px; */
}
.summary-list .item.sub {
    padding-left: 40px;
}
.summary-list .item.sub ul {
    font-size: 13px;
}
.summary-list .qty {
    text-align: right;
}
.summary-list .price {
    text-align: right;
}
.summary-list .total {
    text-align: right;
}
.summary-list.footer {
    font-weight: 600;
    border-top: 1px solid #ececec;
    border-bottom: 1px solid #c6c6c6;
}

.wrap-total {
    text-align: right;
    padding: 0px 20px;
    font-size: 20px;
}
.wrap-total > .total-names {
    font-weight: 600;
}
.wrap-total > .total-numbers {
    flex-grow: unset !important;
    max-width: 90px !important;
}
.wrap-total > .total-numbers > span.number {
    display: flex;
    width: 90px;
}